.intro{
    display: flex;
    height: 25vh;

}

.i-left{
    flex: 1;
}

.i-right{
    flex: 1;
}

.i-left-wrapper{
    padding: 50px;
}

.i-intro{
    font-size: 30px;
    font-weight: 300;
}

.i-name{
    font-size: 60px;
}

.i-title{
    height: 50px;
    overflow: hidden
}

.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: green;
    display: flex;
    align-items: center;
}

.i-image{
    width: 500px;
    height: 500px;
    object-fit: cover;
}

.i-title-wrapper{
    height: 100%;
    /* uncomment when I have more in wrapper */
    /* animation: move 10s ease-in-out infinite alternate */
}

